import React from "react";
import logo from "./logo.svg";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch,
  useLocation,
} from "react-router-dom";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import { AnimatePresence } from "framer-motion";
import "./App.css";

import Footer from "./components/Footer";
import Home from "./pages/Home";
import About from "./pages/About";
import Projects from "./pages/Projects";

class App extends React.Component {
  constructor(props) {
    super(props);
    {
      /* states for navigation bar */
    }
    this.state = {
      title: "William Hong",
      headerLinks: [
        { title: "Home", path: "/" },
        { title: "About", path: "/about" },
        { title: "Projects", path: "/projects" },
      ],
      home: {
        title: "William Hong",
        subTitle: "Learning about us",
        subText: "check me out",
      },
      about: {
        title: "About Me",
      },
      projects: {
        title: "My Projects",
      },
    };
  }

  Locations() {
    const location = useLocation();
  }

  render() {
    return (
      <Router>
        <Container className="p-0" fluid={true}>
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>William Hong</Navbar.Brand>

            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navebar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">
                  Home
                </Link>
                <Link className="nav-link" to="/about">
                  About
                </Link>
                <Link className="nav-link" to="/Projects">
                  Projects
                </Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>

          <Route
            render={({ location }) => (
              <AnimatePresence initial={true} exitBeforeEnter>
                <Switch location={location} key={location.key}>
                  <Route
                    path="/"
                    exact
                    render={() => (
                      <Home
                        title={this.state.home.title}
                        subTitle={this.state.home.subTitle}
                        subText={this.state.home.subText}
                      />
                    )}
                  />

                  <Route
                    path="/about"
                    render={() => <About title={this.state.about.title} />}
                  />

                  <Route
                    path="/projects"
                    render={() => (
                      <Projects title={this.state.projects.title} />
                    )}
                  />
                </Switch>
              </AnimatePresence>
            )}
          />
        </Container>
      </Router>
    );
  }
}

export default App;
