import React from "react";
import { motion } from "framer-motion";

import Hero from "../components/Hero";

function Home(props) {
  return (
    <div>
      <div
        className="content-wrapper"
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          textAlign: "center",
        }}
      >
        <motion.div
          initial={{ opacity: 0 }}
          exit={{ opacity: [0.5, 0] }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <motion.img
            animate={{ opacity: 1 }}
            transition={{ duration: 0.5 }}
            src={require("../components/vector.png")}
            className="manImage"
          />
          <h1 className="display-1 font-weight-bolder">William Hong</h1>
          <h3 className="display-4 font-weight-light">
            Graduate Full Stack Developer
          </h3>
        </motion.div>
      </div>
    </div>
  );
}

export default Home;
