import React from "react";
import Hero from "../components/Hero";
import { motion } from "framer-motion";

function About(props) {
  return (
    <div>
      <div
        className="content-wrapper"
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "top",
          textAlign: "center",
        }}
      >
        <motion.div
          transition={{ duration: 0.7 }}
          exit={{ opacity: [0.5, 0], x: -500 }}
        >
          <motion.h1
            animate={{
              opacity: [0.5, 0.75, 1],
              y: 20,
            }}
            transition={{ duration: 1 }}
            className="display-3 font-weight-light"
          >
            About Me
          </motion.h1>

          <motion.div
            animate={{ y: 100, scaleX: 3, scaleY: 2, opacity: [0, 0.1, 0.25] }}
            transition={{ duration: 0.7 }}
            className="textAboutBox"
          ></motion.div>
        </motion.div>
      </div>
      <motion.div
        className="textBox"
        transition={{ duration: 0.7 }}
        animate={{ opacity: [0, 0.5, 1], y: 50 }}
        exit={{ opacity: [0.5, 0], x: -500 }}
      >
        <p className="text">
          Graduate Information Technology / Business student, seeking a graduate
          role focusing on Fullstack development. Despite pursuing a business
          degree my passion is with information technology (programming).
          Therefore, I am seeking employment with regards to my passion , that
          will allow me to utilize my current skill set whilst also refining my
          technical skills.
        </p>
      </motion.div>
    </div>
  );
}

export default About;
