import React from "react";
import SnapOverflow from "../components/Snap1.jpg";
import Uask from "../components/UAsk.jpeg";
import CovidTracker from "../components/Covid.jpg";
import Card from "../components/Card.js";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

class Carousel extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          id: 0,
          title: "StackOverflow",
          subTitle: "Android Application",
          imgSrc: SnapOverflow,
          link: "https://github.com/weelliem/SnapOverflow-master",
          selected: false,
        },
        {
          id: 1,
          title: "UAsk",
          subTitle: "IOS Application",
          imgSrc: Uask,
          link: "https://github.com/weelliem/UAsk",
          selected: false,
        },
        {
          id: 2,
          title: "Covid-19 Tracker",
          subTitle: "Web API [In Progress]",
          imgSrc: CovidTracker,
          link: "https://github.com/weelliem/Covid-Web-API",
          selected: false,
        },
      ],
    };
  }

  handleCardClick = (id, card) => {
    console.log(id);

    let items = [...this.state.items];
    items[id].selected = items[id].selected ? false : true;

    items.forEach((item) => {
      if (item.id !== id) {
        item.selected = false;
      }
    });

    this.setState({
      items,
    });
  };

  makeItems = (items) => {
    return items.map((item) => {
      return (
        <Card
          item={item}
          click={(e) => this.handleCardClick(item.id, e)}
          key={item.id}
        />
      );
    });
  };

  render() {
    return (
      <Container fluid={true}>
        <Row className="d-flex justify-content-around">
          {this.makeItems(this.state.items)}
        </Row>
      </Container>
    );
  }
}

export default Carousel;
