import React, { Component } from "react";
import { motion } from "framer-motion";
import Carousel from "../components/Carousel";

function Projects(props) {
  return (
    <div>
      <div
        className="content-wrapper"
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "bottom",
          textAlign: "center",
        }}
      >
        <motion.div
          className="centre-test"
          transition={{ duration: 0.5 }}
          exit={{ opacity: [0.5, 0], x: -250 }}
        >
          <motion.h1
            style={{ marginRight: 250, opacity: 0 }}
            animate={{
              x: -300,
              opacity: [0.5, 0.75, 1],
            }}
            transition={{ type: "spring", stiffness: 80, duration: 1 }}
            className="display-3 font-weight-light"
          >
            Projects
          </motion.h1>

          <Carousel className="projectsC" />
        </motion.div>
      </div>
    </div>
  );
}

export default Projects;
